import React, { useState } from 'react';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import useScrollTrigger from '@material-ui/core/useScrollTrigger';
import { makeStyles, Theme, createStyles, IconButton, Menu, MenuItem } from '@material-ui/core';
import { MoreVert } from '@material-ui/icons';
import ReporterDialog from '../../dialogs/report-bug';
import AboutDialog from '../../dialogs/about';

interface Props {
  children: React.ReactElement;
}

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      flexGrow: 1,
    },
    menuButton: {
      marginRight: theme.spacing(2),
    },
    title: {
      flexGrow: 1,
    },
  }),
);

function ElevationScroll(props: Props) {
  const { children } = props;
  const trigger = useScrollTrigger({
    disableHysteresis: true,
    threshold: 0,
  });

  return React.cloneElement(children, {
    elevation: trigger ? 4 : 0,
  });
}

export default function Header() {
  const classes = useStyles();
  const [anchorEl, setAnchorEl] = useState<null | HTMLElement>(null);
  const [openReporter, setOpenReporter] = useState(false);
  const [openAbout, setOpenAbout] = useState(false);
  const open = Boolean(anchorEl);

  const handleMenu = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const handleGoPage = () => {
    handleClose();
    window.open("http://arqui2.alumnos.exa.unicen.edu.ar/", "_blank")
  }

  const handleReportBug = () => {
    handleClose();
    setOpenReporter(true);
  }

  const handleOpenAbout = () => {
    handleClose();
    setOpenAbout(true);
  }

  return (
    <React.Fragment>
      <ElevationScroll>
        <AppBar>
          <Toolbar>
            <Typography variant="h6" className={classes.title}>Perfiles de Paralelismo</Typography>
            <div>
              <IconButton
                aria-label="account of current user"
                aria-controls="menu-appbar"
                aria-haspopup="true"
                onClick={handleMenu}
                color="inherit"
              >
                <MoreVert />
              </IconButton>
              <Menu
                id="menu-appbar"
                anchorEl={anchorEl}
                anchorOrigin={{
                  vertical: 'top',
                  horizontal: 'right',
                }}
                keepMounted
                transformOrigin={{
                  vertical: 'top',
                  horizontal: 'right',
                }}
                open={open}
                onClose={handleClose}
              >
                <MenuItem onClick={handleGoPage}>P√°gina de la c√°tedra</MenuItem>
                <MenuItem onClick={handleReportBug}>Reportar bug</MenuItem>
                <MenuItem onClick={handleOpenAbout}>Acerca de</MenuItem>
              </Menu>
            </div>
          </Toolbar>
        </AppBar>
      </ElevationScroll>
      <ReporterDialog open={openReporter} setOpen={(value: boolean) => setOpenReporter(value)} />
      <AboutDialog open={openAbout} setOpen={setOpenAbout} />
    </React.Fragment>
  );
}