import { Dialog, DialogTitle, DialogContent, DialogContentText, DialogActions, Button } from '@material-ui/core';
import React from 'react';

interface ReporterDialog {
  open: boolean;
  setOpen: (value: boolean) => void;
}

const ReporterDialog = ({open, setOpen}: ReporterDialog) => {
  const handleClose = () => {
    setOpen(false);
  }

  const handleSendMail = () => {
    handleClose();
    window.open('mailto:martinmenchon@gmail.com?subject=Bug en Perfiles de Paralelismo')
  }

  return (
    <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">{"Whoopsie üò¨"}</DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            Lo bueno de reportar bugs, es que los arreglan, ¬øno?. Explicanos un poco de que trata en un mail.
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="secondary">
            Cancelar
          </Button>
          <Button onClick={handleSendMail} color="primary" autoFocus>
            Enviar mail
          </Button>
        </DialogActions>
      </Dialog>
  )
}

export default ReporterDialog;
